.Navbar {
    display:flex;
    justify-content: space-around;
    align-items: center;
    background-color: #25D366;
    position: sticky;
    top: 0;
    z-index: 1;
}
.navIcon {
    height: 60px;
    background-color: white;
    border-radius: 50%;
    margin: 20%;
    cursor: pointer;
}