}
.searchContainer {
    position: relative;
}
input[type="search"] {
    
	display: block; 
    width: 100%; 
    font-family: 'Permanent Marker', cursive;
	letter-spacing: 1px;        
	text-shadow: 0 0 2px black;       
    font-size: large;
    border: 1px solid grey;
    border-radius: 100px;
    padding: 2px 23px 2px 30px;
    outline: 0;
    height: 25px;
}
input[type="submit"] {  
	display: block; 
    width: 50%; 
    font-family: 'Architects Daughter', cursive;
    letter-spacing: 1px;     
	text-shadow: 0 0 2px black;       
    border: 1px solid aliceblue;
    border-radius: 100px;
    height: 25px;
    cursor: pointer;
}
input[type="submit"]:hover {  
    color: black;   
    border: 1px solid black;
    font-weight: 900;
	text-shadow: 0 0 2px white;       
}
.search-icon {
    position: absolute;
    top: 6px;
    left: 8px;
    width: 14px;
}
.optionsSearch {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: absolute;
    color: white;
    text-shadow: 0 0 0.5px black;
    width: 100%;
}
